import React, { useMemo, useState } from 'react'
import { getQuarter } from 'date-fns/esm'

import Flex from 'components/Flex'
import Text from 'components/Text'
import Box from 'components/Box'

const useYearControl = () => {
  const [startYear, setStartYear] = useState(() => new Date().getFullYear())
  const [startQuarter, setStartQuater] = useState(() => getQuarter(new Date()))
  const yearControl = useMemo(() => (
    <Flex as="label" my="1em" alignItems="center">
      <Text mr="0.5em">起始年分</Text>
      <Box width="10em">
        <Box
          as="input"
          maxWidth="100%"
          type="number"
          lineHeight="2"
          value={startYear}
          onChange={e => setStartYear(+e.target.value)}
        />
      </Box>
    </Flex>
  ), [startYear])
  const quaterControl = useMemo(() => (
    <Flex as="label" mx="0.25rem" my="1em" alignItems="center">
      <Text mr="0.5em">起始季</Text>
      <Box width="6em">
        <Box
          as="select"
          px="0.5em"
          py="0.5em"
          onChange={e => setStartQuater(+e.target.value)}
          value={startQuarter}
        >
          <option value="1">Q1</option>
          <option value="2">Q2</option>
          <option value="3">Q3</option>
          <option value="4">Q4</option>
        </Box>
        {/* <Box
          as="input"
          maxWidth="100%"
          type="number"
          lineHeight="2"
          value={startQuarter}
          onChange={e => setStartQuater(+e.target.value % 4 || 4)}
        /> */}
      </Box>
    </Flex>
  ), [startQuarter])
  return {
    startYear,
    yearControl,
    startQuarter,
    quaterControl
  }
}

export default useYearControl
