import { useMemo } from 'react'

const useTableHeader = (data, slice = 1) => {
  return useMemo(() => {
    if (!data) return []
    const doubleHeader = data[0].some(h => !h)
    if (doubleHeader) {
      const colLength = data[0].length
      let groupMemo = []
      // 空白代表要group ['a', 'b', '', 'c']
      // b後面的空白要跟b group一起，所以先反轉陣列，從後面檢查回來，只要是空白，就等到下一個值出現然後把他們group起來
      return data[0].slice(0).reverse().reduce((all, h, i) => {
        // 檢查有沒有值
        if (h) {
          // 檢查有沒有之前先標記要變成group的
          if (groupMemo.length) {
            // if true 把現在這一格也加入group
            groupMemo.push(i)
            all.push({
              Header: h,
              columns: groupMemo.slice().reverse().map(j => ({
                Header: data[1][colLength - j - 1],
                accessor: d => d[colLength - j - 1],
                id: `${i}-${j}`,
              })),
            })
            // 清空group的標記
            groupMemo = []
          } else {
            // if false 就是一般的header
            all.push({ Header: h, accessor: d => d[colLength - i - 1], id: i })
          }
        } else {
          // 沒有值，之後要跟別人group，先暫存起來
          groupMemo.push(i)
        }
        return all
      }, [])
        .reverse() // 最後記得再反轉回來
        .slice(slice) // 然後去掉Title
    }
    return data[0].map((h, i) => ({
      Header: h,
      accessor: d => d[i],
      id: i,
    })).slice(slice) // 同樣去掉Title
  }, [data, slice])
}

export default useTableHeader
