import React, { useState, useMemo, cloneElement } from 'react'

import Box from '../components/Box'
import Flex from '../components/Flex'
import Button from '../components/Button'
import Dropdown from '../components/Dropdown'

import useGapi from '../contexts/gapi/useGapi'

const cleanSheetData = (data) => {
  const header = data[0].map((h, i) => data.some(d => d[i]))
  return data.map((r) => r.filter((d, i) => header[i]))
}

const SheetLoader = ({ children, name }) => {
  const {
    loadSheets,
    loading,
    sheets,
    loadSheetData,
    sheetData,
    SpreadsheetIdInput,
  } = useGapi()
  const [selectedSheet, setSheet] = useState('')
  const [compareSheet, setCompareSheet] = useState('')

  const data = useMemo(() => {
    if (sheetData && sheetData[selectedSheet]) {
      return cleanSheetData(sheetData[selectedSheet])
    }
    return null
  }, [sheetData, selectedSheet])
  const compareSheetData = useMemo(() => {
    return compareSheet && sheetData[compareSheet] && cleanSheetData(sheetData[compareSheet])
  }, [sheetData, compareSheet])
  return (
    <>
      <Flex>
        <SpreadsheetIdInput name={name} flex="1" />
        <Box ml="1em">
          <Button disabled={loading} my="1em" onClick={loadSheets}>{loading ? 'Loading...' : '載入資料表'}</Button>
        </Box>
      </Flex>
      {sheets && sheets.length > 0 && (
        <>
          <Flex>
            <Dropdown
              width={1 / 2}
              label="選擇資料表"
              onChange={(selected) => {
                if (selected) {
                  setSheet(selected.value)
                  setCompareSheet('')
                  loadSheetData(selected.value)
                } else {
                  setSheet('')
                }
              }}
              disabled={loading}
              options={sheets.map(sheet => sheet.title)}
              value={selectedSheet}
            />
            <Dropdown
              width={1 / 2}
              label="對比資料表"
              onChange={(selected) => {
                if (selected) {
                  setCompareSheet(selected.value)
                  loadSheetData(selected.value)
                } else {
                  setCompareSheet('')
                }
              }}
              options={sheets.map(sheet => sheet.title).filter(title => title !== selectedSheet)}
              disabled={loading || !selectedSheet}
              value={compareSheet}
            />
          </Flex>
          {cloneElement(children, { name, data, compareSheetData, selectedSheet })}
        </>
      )}
    </>
  )
}

export default SheetLoader
